<template>
    <div>
        <top-nav title="添加运费模板"></top-nav>
        <div class="wyu-main" v-loading="loading">
            <div class="wyu-block">
                <el-form :model="form" ref="form" style="padding: 0" size="mini" label-width="120px">
                    <div class="wyu-title">
                        <h2>基本参数</h2>
                    </div>
                    <el-form-item label="模板标题">
                        <el-input v-model="form.template_name" style="width:500px;" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="计价方式">
                        <el-radio-group v-model="form.type">
                            <el-radio :label="0">计件</el-radio>
                            <el-radio :label="1">计重量</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="默认（全国）">
                        <el-switch :inactive-value="0" :active-value="1" v-model="form.is_enable_default" @change="setDefault"	> </el-switch>
                    </el-form-item>
                    <div class="wyu-title">
                        <h2>区域选择</h2>
                    </div>
                    <el-form-item label-width="10px">
                        <el-button @click="addConfig" style="margin-bottom: 10px;" >新增自定义区域</el-button>
                        <el-table
                                :data="form.config"
                                border
                                size="mini"
                                style="width: 100%">
                            <el-table-column prop="date"   label="配送区域">
                                <template slot-scope="scope">
                                    <div v-if="scope.row.is_default == 1">全国</div>
                                    <div v-if="scope.row.is_default ==0 ">
                                        <div>{{scope.row.region.toString(',')}}</div>
                                        <region-select :regions="scope.row.region_ids" :index="scope.$index"  @selReg="callRegion"></region-select>
                                    </div>
                                </template>
                            </el-table-column>
                            <el-table-column  :label="'首'+unit[form.type]" width="180">
                                <template slot-scope="scope">
                                <el-input  v-model="scope.row.first_unit"  size="mini">
                                    <span slot="suffix" class="e">{{unit_type[form.type]}}</span>
                                </el-input>
                                </template>
                            </el-table-column>
                            <el-table-column  label="运费" width="180">
                                <template slot-scope="scope">
                                <el-input   v-model="scope.row.first_money" size="mini">
                                    <span slot="suffix" class="e">元</span>
                                </el-input>
                                </template>
                            </el-table-column>
                            <el-table-column  :label="'续'+unit[form.type]" width="180">
                                <template slot-scope="scope">
                                <el-input  v-model="scope.row.continue_unit"  size="mini">
                                    <span slot="suffix" class="e">{{unit_type[form.type]}}</span>
                                </el-input>
                                </template>
                            </el-table-column>
                            <el-table-column   label="运费" width="180">
                                <template slot-scope="scope">
                                <el-input v-model="scope.row.continue_money"  size="mini">
                                    <span slot="suffix" class="e">元</span>
                                </el-input>
                                </template>
                            </el-table-column>
                            <el-table-column  label="操作" width="80">
                                <template slot-scope="scope">
                                    <el-button v-if="scope.row.is_default == 0" type="text" @click="delConfig(scope.$index)">删除</el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                    </el-form-item>
                </el-form>
            </div>
        </div>
        <div style="margin-top: 20px"></div>
        <div class="wyu-btn-div">
            <el-button size="small" type="primary" @click="createArea(true)">立即创建</el-button>&nbsp;
            <router-link to="/shipping/area/index/" style="color: #ffffff;">
                <el-button size="small">返回列表</el-button>
            </router-link>
        </div>
    </div>
</template>

<script>
    import * as api from "@/api";
    import TopNav from "../../../components/TopNav";
    import RegionSelect from "../components/Region";

    export default {
        name: 'shippingAraeCreate',
        components: {RegionSelect, TopNav},
        data() {

            return {
                unit:['件','重'],
                unit_type:['件','克'],
                //整体列表 菊花
                loading: false,
                form: {
                    template_name: '',  // 标题
                    type: 0,
                    is_enable_default: '',
                    config:[
                        {first_unit:0,first_money:0,continue_money:0,continue_unit:0,is_default:0,region:[],region_ids:[]}
                    ],

                },
                region: [],
            }
        },
        watch: {
            filterText(val) {
                this.$refs.tree.filter(val);
            }
        },
        created() {

        },
        methods: {
            setDefault(val){
                if(val == 1){
                    this.form.config.unshift( {first_unit:0,first_money:0,continue_money:0,continue_unit:0,is_default:1,region:[],region_ids:[]})
                }else{
                    this.form.config.splice(0,1);
                }
            },
            callRegion(val){
                let Ids = [];
                let region = [] ;
                val.select_region.forEach(val => {
                    Ids.push(val.region_id);
                    region.push(val.label);
                });
                this.form.config[val.index].region = region;
                this.form.config[val.index].region_ids = Ids;
            },
            addConfig(){
                this.form.config.push( {first_unit:0,first_money:0,continue_money:0,continue_unit:0,is_default:0,region:[],region_ids:[]})
            },
            delConfig(index){
                this.form.config.splice(index,1);
            },
            //提交信息
            createArea() {
                this.loading = true;
                api.post("/shop/admin.freight/create", {
                    appToken: this.$store.getters.appToken,
                    template_name:this.form.template_name ,  // 标题
                    type: this.form.type,
                    is_enable_default: this.form.is_enable_default,
                    config: JSON.stringify(this.form.config),
                }).then(() => {
                    this.$message({
                        message: '恭喜你，操作成功',
                        type: 'success'
                    });
                    this.loading = false;
                    this.$router.push("/shipping/area/index/");
                }).catch(() => {
                    this.loading = false;
                });
            },


        }
    }
</script>


